import React from "react";
import styled from "styled-components";

const StyDiv = styled.div`
  background-color: orange;
  padding: 10px;
  margin: 30px 0;
  border-radius: 4px;
  color: #f2f3f4;
`;

const Component = () => {
  return (
    <StyDiv>
      请您登录使用本网站
      <br/>
      如果没有账号请注册
    </StyDiv>
  );
}

export default Component;